<template>
  <el-drawer title="逻辑展示" direction="btt" size="75%">
    <el-collapse v-model="activeNames" @change="handleChange">
      <el-collapse-item title="搭建" name="1">
        <csTitle title="棋盘搭建" />
        <p>
          创建一个二维数组的方法，数组的尺寸由<csSpanCode
            code="form.size"
          />和<csSpanCode code="maxPx.value" />决定
        </p>
        <csCode :code="code" type="js" />
        <p>
          先来看一下<csSpanCode code="Array.from()" />这个函数。<csSpanCode
            code="Array.from()"
          />方法从一个类似数组或可迭代对象中创建一个新的、浅拷贝的数组实例。这里的<csSpanCode
            code="{ length: form.size }"
          />是一个含有<csSpanCode code="length" />属性的对象，<csSpanCode
            code="Array.from()"
          />会将其视为数组并创建一个长度为<csSpanCode
            code="form.size"
          />的新数组。
        </p>
        <p>
          <csSpanCode
            code="Array.from()"
          />的第二个参数是一个映射函数，用于对每个元素进行处理。在这里，它返回另一个数组<csSpanCode
            code="Array(form.size > maxPx.value ? maxPx.value :form.size).fill('')"
          />。这个数组的长度由<csSpanCode
            code="form.size > maxPx.value ?maxPx.value : form.size"
          />
          这个三元运算符决定，即如果<csSpanCode
            code="form.size"
          />大于<csSpanCode code="maxPx.value" />，则长度为<csSpanCode
            code="maxPx.value"
          />
          ，否则为<csSpanCode code="form.size" />。然后<csSpanCode
            code=".fill('')"
          />
          将数组填充为空字符串。
        </p>
      </el-collapse-item>
      <el-collapse-item title="校验" name="2">
        <csTitle title="规则校验" />
        <p>
          五子棋的胜利规则是：在棋盘上先形成5个或更多相同颜色棋子的直线（水平、垂直或对角线）的一方获胜。因此，我们可以在落子的时候对棋盘进行检查
          是否在（水平、垂直或对角线）直线上形成了5子连珠。
        </p>
        <el-tabs type="border-card">
          <el-tab-pane label="全局式校验">
            <div class="carousel">
              <el-carousel :interval="3000" indicator-position="none" height="300px">
                <el-carousel-item v-for="item in ruleImg" :key="item">
                  <el-image :src="item.src" fit="cover"/>
                </el-carousel-item>
              </el-carousel>
            </div>
          </el-tab-pane>
          <el-tab-pane label="八爪鱼校验">Config</el-tab-pane>
        </el-tabs>
      </el-collapse-item>
    </el-collapse>
  </el-drawer>
</template>

<script setup>
